body {
  padding: 0;
  background-color: #ffffff;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: inline-block;
  box-sizing: border-box;
  min-width: 260px;
  padding: 14px 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  line-height: 20px;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  vertical-align: middle;
  background-color: #ffffff;
  border: 2px solid #444444;
  cursor: pointer;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:focus {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.page-header {
  min-width: 320px;
}
.main-nav {
  position: relative;
  z-index: 2;
}
.main-nav__toggle {
  position: absolute;
  top: 28px;
  right: 30px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}
.main-nav__toggle:focus {
  outline: none;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -3px;
  width: 27px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 2px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec;
}
.main-nav__link {
  display: block;
  box-sizing: border-box;
  width: 260px;
  margin: 0 auto;
  padding: 27px 0 28px 45px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
.main-nav__link--logo {
  width: 113px;
  padding: 22px 0;
}
.main-nav__link[href]:hover {
  opacity: 0.6;
}
.main-nav__link[href]:active,
.main-nav__link[href]:focus {
  opacity: 0.3;
  outline: none;
}
.main-nav__logo {
  display: block;
  margin: 0 auto;
}
.main-nav__additional-info,
.main-nav__item--delivery {
  display: none;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item:not(.main-nav__item--logo),
  .main-nav--closed .main-nav__service-nav {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav::after {
    content: "";
    position: absolute;
    top: 88px;
    width: 100%;
    border-bottom: 1px solid #ececec;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 668px;
    margin: 0 auto;
  }
  .main-nav__item {
    box-shadow: none;
  }
  .main-nav__link {
    width: 157px;
    padding: 19px 0;
    font-size: 15px;
  }
  .main-nav__link--logo {
    width: 113px;
    padding: 21px 0;
  }
  .main-nav__link--catalog {
    margin-right: 10px;
  }
  .main-nav__item--logo {
    order: -2;
    margin-right: 180px;
  }
  .main-nav__logo {
    width: 112px;
    height: auto;
  }
  .main-nav__service-nav {
    order: -1;
  }
  .main-nav__item--delivery {
    display: block;
    margin: 0 0 0 auto;
    padding: 19px 0;
    font-family: "OpenSans", "Arial", sans-serif;
    font-size: 15px;
    line-height: 24px;
    text-align: right;
    color: #444444;
  }
}
@media (min-width: 1150px) {
  .main-nav__list {
    width: 1050px;
  }
  .main-nav__item--catalog {
    order: -3;
    border-right: 1px solid #ececec;
  }
  .main-nav__item--form {
    order: -3;
    margin-left: 35px;
  }
  .main-nav__item--logo {
    width: 150px;
    margin: 0 100px 0 auto;
  }
  .main-nav__link {
    width: 184px;
    padding: 32px 0;
    font-size: 17px;
  }
  .main-nav__link--logo {
    width: 150px;
    padding: 8px 0;
  }
  .main-nav__link--catalog {
    margin-right: 0;
  }
  .main-nav__logo {
    width: 149px;
    height: auto;
  }
  .main-nav__additional-info {
    display: block;
  }
  .main-nav__additional-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }
  .main-nav__additional-item {
    margin-right: 35px;
  }
  .main-nav__additional-link {
    padding: 17px 0;
    font-size: 15px;
  }
}
.service-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.service-nav__item {
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec;
}
.service-nav__link {
  position: relative;
  display: block;
  box-sizing: border-box;
  width: 260px;
  margin: 0 auto;
  padding: 27px 0 27px 45px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
.service-nav__link:hover {
  color: #666666;
}
.service-nav__link:hover .service-nav__icon {
  opacity: 1;
  fill: #63d1bb;
}
.service-nav__link:active,
.service-nav__link:focus {
  color: #b2b2b2;
  outline: none;
}
.service-nav__link:active .service-nav__icon,
.service-nav__link:focus .service-nav__icon {
  opacity: 0.3;
  fill: #63d1bb;
}
.service-nav__icon {
  position: absolute;
  top: 30px;
  left: 0;
  opacity: 0.2;
  fill: #231f20;
}
@media (min-width: 768px) {
  .service-nav__list {
    display: flex;
  }
  .service-nav__item {
    box-shadow: none;
    border-left: 1px solid #ececec;
  }
  .service-nav__link {
    margin: 0;
    padding: 32px 0;
  }
  .service-nav__link.service-nav__link--search {
    width: 82px;
    font-size: 0;
  }
  .service-nav__link.service-nav__link--cart {
    width: 265px;
    text-align: right;
  }
  .service-nav__icon {
    top: 34px;
    left: 32px;
  }
}
.page-main__title-wrapper {
  min-width: 320px;
  padding-top: 70px;
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-position: center -17px;
  background-repeat: no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-main__title-wrapper {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}
.page-main__title {
  width: 260px;
  margin: 0 auto 123px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 25px;
  font-weight: bold;
  color: #000000;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
}
.page-main__title--inner-page {
  width: auto;
  min-width: 320px;
  margin-bottom: 30px;
  padding: 74px 0;
  background-color: #f1f1f1;
}
.page-main__actual,
.page-main__about-us,
.page-main__review,
.page-main__contacts,
.page-main__booking {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-main__title-wrapper {
    position: relative;
    margin-top: -62px;
    padding-top: 155px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position: center top;
  }
  .page-main__title-wrapper::after,
  .page-main__title--inner-page::after {
    content: "";
    position: absolute;
    bottom: -10px;
    width: 100%;
    height: 23px;
    background-image: url("../img/bg-zigzag-fill.svg");
    background-position: center;
    background-size: contain;
    background-repeat: repeat-x;
  }
  .page-main__title {
    width: 668px;
    margin-bottom: 490px;
    font-size: 43px;
    line-height: 50px;
  }
  .page-main__title--inner-page {
    position: relative;
    width: auto;
    margin: -62px 0 78px;
    padding: 95px 0 82px;
  }
  .page-main__title--inner-page::after {
    left: 0;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .page-main__title-wrapper {
    background-image: url("../img/hero-tablet@2x.jpg");
    background-size: 1150px auto;
  }
}
@media (min-width: 1150px) {
  .page-main__title-wrapper {
    background-image: url("../img/hero-desktop@1x.jpg");
  }
  .page-main__title-wrapper::after {
    bottom: -15px;
    height: 37px;
  }
  .page-main__title {
    width: 700px;
    margin-bottom: 480px;
    font-size: 53px;
    line-height: 57px;
  }
  .page-main__title--inner-page {
    width: auto;
    padding: 95px 0 105px;
    margin-bottom: 126px;
  }
  .page-main__title--inner-page::after {
    bottom: -15px;
    height: 34px;
  }
}
@media (min-width: 1150px) and (min-resolution: 144dpi), (min-width: 1150px) and (min-resolution: 1.5dppx) {
  .page-main__title-wrapper {
    background-image: url("../img/hero-desktop@2x.jpg");
    background-size: 1150px auto;
  }
}
