.type-list__label {
  position: relative;
  display: block;
  min-height: 40px;
  padding: 11px 0 11px 62px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  font-weight: bold;
  line-height: 18px;
  text-align: left;
  color: #000000;
  cursor: pointer;
}
.type-list__label::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  display: block;
  width: 36px;
  height: 36px;
  border: 2px solid #000000;
  border-radius: 50%;
}
.type-list__input:hover + .type-list__label::before {
  border-color: #63d1bb;
}
.type-list__input:active + .type-list__label::before,
.type-list__input:focus + .type-list__label::before {
  border-color: #ececec;
}
.type-list__input:checked + .type-list__label::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 12px;
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #63d1bb;
}
